.main {
  background-color: cornflowerblue;
  min-height: 200px;
  display: flex;
  flex-direction: column;
  align-content: center;
  width: 100%;
  margin: auto;
  font-size: calc(10px + 2vmin);
  color: white;
}

.key:hover {
  position: absolute;
  stroke: red;
  fill: red;
  z-index: 2 !important;
}

.key:not(:hover) {
  position: absolute;

  z-index: 1 !important;
}

svg {
  align-self: center;
  pointer-events: none;
  min-width: 528px;
}

path {
  pointer-events: auto;
}

button {
  max-width: 310px;
  margin: auto;
}
